.stars {
  display: flex;

  .star {
    background-image: url(../images/star-active.svg);
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;
  }

  .star:last-child {
    background-image: url(../images/star.svg);
  }
}
